.grid {
   grid-template-columns: 1fr;

   .container {
      @extend %container;
   }

   &__title {
      width: 160px;
      margin: 0 auto;
      @include text(28px, 400, 34px, $textColor);
   }

   &__img {
      object-fit: cover;
      width: 100%;
   }

   .text {
      @include text(14px, 400, 20px, $textColor);
   }

   &__list-item {
      margin-bottom: 20px;
   }
}